.wp-block-create-block-team-members {
	display: block;
	@for $i from 1 through 6 {
		&.has-#{$i}-columns {
			.block-editor-inner-blocks
				> .block-editor-block-list__layout
				> [data-type='create-block/team-member'] {
				width: calc(100% / #{$i} - 20px);
			}
			.block-list-appender {
				align-self: stretch;
				margin: 10px;
				width: calc(100% / #{$i} - 20px);
				border: 1px dashed;
				display: flex;
				align-items: center;
				justify-content: center;
				min-height: 70px;
				position: relative;
			}
		}
	}
	> .block-editor-inner-blocks > .block-editor-block-list__layout {
		display: flex;
		flex-wrap: wrap;
		margin-left: -10px;
		margin-right: -10px;
		[data-type='create-block/team-member'] {
			max-width: none;
			margin: 0;
		}
		> [data-type='create-block/team-member'] {
			margin: 10px;

			.block-editor-media-placeholder {
				margin-bottom: 20px;
			}
			.wp-block-blocks-course-team-member-img {
				position: relative;
				margin-bottom: 20px;

				&.is-loading {
					opacity: 0.3;
				}
				.components-spinner {
					position: absolute;
					left: 0;
					top: 0;
					right: 0;
					bottom: 0;
					margin: 0;
				}
			}
			h4 {
				color: #222;
				font-size: 22px;
				font-weight: bold;
				margin: 0;
			}
			p {
				color: #565656;
				font-size: 16px;
				font-weight: 300;
				margin-bottom: 5px;
				margin-top: 5px;
			}
		}
	}
}
